@mixin flexCenter {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .main-wrapper-content{
    @include flexCenter;
  }
 
  .modal-content {
    position: relative;
    background: white;
    padding: 15px;
    width: 366px;
  }
  
  .signIn-text {
    text-align: center;
    font-family: Regular-font;
  }
  
  .close-element {
    position: relative;
    left: 96%;
    cursor: pointer;
    transition: 0.2s all;
    width: 15px;
    height: 15px;
  
    &:hover {
      opacity: 0.5;
    }
  }
  
  .modal-content form {
    @include flexCenter;
    padding: 15px;
    flex-direction: column;
    width: 100%;
    height: auto;
  
    .input-block {
      width: 100%;
      margin-top: 15px;
      height: 70px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
  
      input {
        border: 1px solid rgba(0, 0, 0, 0.3);
        width: 100%;
        height: 40px;
        padding: 0 15px;
        font-size: 18px;
        outline: none;
        color: black;
        font-weight: 500;
        font-family: Regular-font;
      }
  
      input[type="password"] {
        letter-spacing: 10px;
      }
  
      p {
        color: black;
        font-size: 20px;
        text-transform: uppercase;
        text-align: left;
        width: 100%;
      }
    }
  
    .forgot-password {
      @include flexCenter;
      transition: 0.3s all;
      margin-top: 20px;
      cursor: pointer;
      height: 100%;
      color: #464646;
      background-image: linear-gradient(#464646, #464646);
      background-position: 50% 100%;
      background-repeat: no-repeat;
      background-size: 0% 1px;
      height: 25px;
  
      svg {
        margin-left: 10px;
        height: 20px;
        width: 20px;
      }
  
      &:hover {
        background-size: 100% 1px;
      }
    }
  
  .button-redirect-to-registration {
      color: black;
      transition: 0.3s all;
      font-size: 20px;
      background: transparent;
      border: none;
      margin: 30px 0 20px 0;
      text-transform: uppercase;
      cursor: pointer;
      font-family: Regular-font;
    }
  
  .button-to-signIn {
      transition: 0.3s all;
      cursor: pointer;
      margin-top: 30px;
      align-items: center;
      background: rgb(200, 0, 0);
      color: #fff;
      display: flex;
      font-size: 20px;
      height: 40px;
      justify-content: center;
      text-transform: uppercase;
      border: none;
      width: 165px;
      font-family: Regular-font;
  
      svg {
        margin-left: 10px;
      }

      &:hover{
        background-color: red;
      }
    }
  
    
    .button-redirect-to-registration:hover {
      color: #767676;
    }
  }